.movie-page {
    padding: 30px 0;
  }
  
  .movie-page .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    
  }
  .movie-page .header .heading{
    margin-left: 160px;
   ;
    
  }
  
  .count-pill {
    margin-right: 200px;
    display: block;
    background-color: var(--secondary);
    padding: 5px 15px;
    font-size: 1.125rem;
    font-weight: 600;
    border-radius: 999px;
    color: var(--primary);
  }
  
  .movie-page .heading {
    
    color: var(--primary);
    margin: 0;
    color: rgb(87, 86, 86);
  }
  
  .movie-page .movie-grid {
    margin-left: 160px;
    margin-right: 180px;
    
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-gap: 30px;
  }
  
  
    .movie-grid {
      grid-template-columns: repeat(4, 1fr);
    
  }
  
  .movie-card {
    width: 100%;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    border: 0;
    display: block;
  }
  
 
  .movie-card .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid transparent;
    transition: all 0.3s ease;
  }
  
  .movie-card:hover .overlay {
    border: 3px solid var(--secondary);
  }
  
  .movie-card .inner-card-controls {
    display: inline;
    background-color: red;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(0, 0, 0, 0.8);
    border-radius: 5px;
    padding: 3px;
    opacity: 0;
    transition: all 0.3s ease;
  }
  
  .movie-card:hover .inner-card-controls {
    opacity: 1;
  }
  
  .ctrl-btn {
    color: #fefefe;
    background-color: transparent;
    border: none;
    transition: all 0.3s ease;
    font-size: 1.25rem;
    padding: 5px;
    margin: 0;
  }
  
  .ctrl-btn:hover {
    color: var(--secondary);
    cursor: pointer;
  }
  
  .movie-page .no-movies {
    color: #c4c4c4;
    font-size: 3rem;
    text-align: center;
  }
